@import "colors";
@import "mixins";
@import "icons";
@import "typography";
@import "global";
@import "fonts";
@import "header";
@import "footer";

html,
body {
    height: 100%;
    height: 100%;
}

body {
    position: relative;
    font-family: "Roboto" sans-serif;
}

.grid-container {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(7, 1fr);
    grid-template-areas: 
        "header-box header-box header-box header-box" 
        "banner-box banner-box banner-box banner-box"
        "banner-box banner-box banner-box banner-box"
        "banner-box banner-box banner-box banner-box"
        "banner-box banner-box banner-box banner-box"
        "banner-box banner-box banner-box banner-box"
        "arrow-down-box arrow-down-box arrow-down-box arrow-down-box";

    position: relative;
    z-index: 4;

    overflow: hidden;

}

///////////////////
// modal start
.modal-box {
    // hide modal
    display: none;
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.5);
    cursor: pointer;

    justify-content: center;
    align-items: center;
    .modal-box__container {
        position: relative;
        padding: 5px;
        background-color: $ltGray;     
        display: flex;
        max-width: 500px;
        flex-wrap: wrap;
        flex-direction: column;
    
        .modal-box__item {
            flex: 0 1 50%;
            @include flexCHV;

            p {
                color: $dkGray;
                font-size: 35px;
                &:hover span {
                    color: indianred;
                }
            }

            img {
                max-width: 100%;
                height: auto;
                object-fit: cover;
            }

        }

        .closeBtn {
            position: absolute;
            top: 0;
            padding: 10px;
            right: 0;
            cursor: pointer;
            background: $white;

            svg {
                fill: #155799;
            }
        }
    }
}

////////////////////
// banner section
.banner-box { 
    grid-area: banner-box;

    background: url("./../src/img/laptop-glasses.jpeg") center;
    background-size: cover;
    background-repeat: no-repeat;

    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;

    .wrapper-inner {
        .banner-box__content {
            flex-direction: column;
            display: flex;
            position: relative;
            z-index: 5;
            color: $white;

            .cta {
                background: rgba(255, 255, 255, 0.7);
                color: #000;
                text-decoration: none;
                font-size: 30px;
                text-transform: uppercase;
                padding: .4em;
                transition: all .1s ease-in-out;
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
                font-family: ns, Roboto, Montserrat, sans-serif;
                overflow: hidden;

                @include breakpoint(mobile) {
                    font-size: 20px;
                }

                .svg__icon--color {
                    fill: #000;
                    padding: 0 0 0 10px;
                }

                &:hover {
                    color: $blue;
                    background: #000;
                    &:hover .svg__icon--color {
                        fill: $blue;
                    }
                }
            }

            h1 {
                font-family: ns, Roboto, sans-serif;
                text-transform: uppercase;
                font-size: 60px;
                order: -1;
                padding: 0 0 2px;

                @include breakpoint(mobile) {
                    font-size: 40px;
                }

                span {
                    color: #eee;
                    display: inline-block;
                    background: rgba(0, 0, 0, 0.7);
                    margin-bottom: 5px;
                    padding: 0 10px;
                    transition: all .1s ease-in-out;

                    @include breakpoint(mobile) {
                        padding: 10px
                    }
                }
            }
        }

        // banner text hover
        .banner-box__content .cta:hover ~ h1 span {
            color: #000;
            background: #fff;
        }
    }

    &::after {
        content: "";
        background-color: rgba(12, 12, 12, 0.5);
        position: absolute;
        z-index: 4;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
}


////////////////////
// education box
section.education-box {
    .wrapper-inner {

        .container-education {

            .acc {
                overflow: hidden;
                padding: 0;

                li {
                    // list-style-type: none;
                    padding: 0;

                    .acc_ctrl {
                        display: flex;
                        background: #FFFFFF;
                        border: none;
                        border-bottom: solid 1px #F2F2F2;
                        cursor: pointer;
                        outline: none;
                        position: relative;
                        text-align: center;
                        width: 100%;
                        
                        h2 {
                            color: #156a85;
                        }
                    }
                      
                    .acc_ctrl:before {
                        background: #44596B;
                        content: '';
                        height: 2px;
                        margin-right: 37px;
                        position: absolute;
                        right: 0;
                        top: 50%;
                        transform: rotate(90deg);
                        transition: all 0.2s ease-in-out;
                        width: 14px;
                    }
                      
                    .acc_ctrl:after {
                        background: #44596B;
                        content: '';
                        height: 2px;
                        margin-right: 37px;
                        position: absolute;
                        right: 0;
                        top: 50%;
                        width: 14px;
                    }

                    .acc_ctrl.active:before {
                        transform: rotate(0deg);
                    }
                      
                    .acc_ctrl.active h2, .acc_ctrl:focus h2 {
                        position: relative;
                    }

                    .acc_panel {
                        position: relative;
                        padding: 5px;
                        background-color: #155799;
                        background-image: linear-gradient(120deg,#155799,#159957);
                        display: none;
                        overflow: hidden;

                        p {
                            padding: 5px;
                            background: white;
                        }
                    }
                }
            }
        }
    }
}


  
.arrow-down-box { 
    grid-area: arrow-down-box;

    background-color: #d8d8d8;

    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;
    z-index: 5;

    .bounce-animation {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 50px;
        width: 50px;
        background-color: #c5c5c5;
        border-radius: 3px;
        padding: 10px;
        color: #fff;
        line-height: 50px;
        margin: 5px;

        animation: bounce 5s infinite;
        transition: 0.5s ease-in-out;
      
      
        i {
            border: solid $blue;
            border-width: 0 5px 5px 0;
            display: inline-block;
            padding: 3px;
            width: 30px;
            height: 30px;
        }
      
        .down {
            transform: rotate(45deg);
            -webkit-transform: rotate(45deg);
        }
    } 
}

@keyframes bounce {
    0%,
    20%,
    50%,
    80%,
    100% {
      -moz-transform: translateY(0);
      -ms-transform: translateY(0);
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
    40% {
      -moz-transform: translateY(-10px);
      -ms-transform: translateY(-10px);
      -webkit-transform: translateY(-10px);
      transform: translateY(-10px);
    }
    60% {
      -moz-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
      -webkit-transform: translateY(-5px);
      transform: translateY(-5px);
    }
  }

