///////////////////////
// gallery box 
.gallery-box2 {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    grid-gap: 5px;

    @include breakpoint(mobile) {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }

    .item-grid2 {
        position: relative;
        overflow: hidden;

        @include breakpoint(tablet) {
            &:last-child {
                display: flex;
                width: 100%;
                justify-content: center;
            }
        }      

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        .item__overlay2 {
            position: absolute;
            z-index: 2;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;

            background-color: rgba(12,12,12,.5);
      
            transform: translateY(100%);
            transition: 0.2s;

            @include flexCHV;
            cursor: pointer;

            button {
                border: 2px solid white;
                color: white;
                text-transform: uppercase;
                background-color: rgba(0, 0, 0, 0.7);
                padding: 5px;
                cursor: pointer;
                @include flexCHV;
                outline: transparent;
            }
        }
    }

    .item-grid2:hover .item__overlay2 {
        transform: translateY(0);
      }
}

.close {
    background: none;
    color: black;
    border: 0;
    cursor: pointer;
    outline: transparent;

    display: flex;
    align-items: center;

    padding: 10px;
    position: absolute;
    top: 0;
    right: 0;
    background: aliceblue;
}

////////////////////
// overlay box
.overlay-box2 {
    position: fixed;
    background: rgba(0, 0, 0, 0.7);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: none;
    z-index: 2;
}

.overlay-box2.open {
    display: flex;
    z-index: 7;
    justify-content: center;
    align-items: center;
}

.overlay-inner2 {
    background: white;
    width: 700px;
    padding: 5px;
    position: relative;
}

.overlay-box2 img {
    width: 100%;
}

/////////////////////
// links box
.links-box {
    background-color: #155799;
    background-image: linear-gradient(120deg,#155799,#159957);
    .wrapper-inner {
        .links-box__container {
            display: flex;
            margin: auto;
            width: 100%;

            @include breakpoint(mobile) {
                flex-wrap: wrap;
            }

            a.links-box__item {
                background: $white url("./../src/img/bg-wire.svg");
                overflow: hidden;
                position: relative;
                flex: 1;
                margin: 5px;
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 20px;
                transition: flex .4s;

                @include breakpoint(mobile) {
                    flex: 1 1 calc(50% - 20px);
                }

                .overlay-links {
                    background:$white url("./../src/img/bg-wire.svg");
                    display: flex;
                    padding: 0 10px;
                    position: absolute;
                    top: 0px;
                    right: 0;
                    bottom: 0;
                    left: 0px;
                    transform: translateY(-90px);
                    transition: all 0.4s ease-in-out;
                    align-items: center;
                    justify-content: center;

                    .overlay-links__inner {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        position: absolute;
                        width: 100%;
                        h2 {
                            padding: 0 10px 0 0;
                            color: $ltGray;
                        }
                    }

                }

                &:hover {
                    flex: 3;
                }

                &:hover .overlay-links {
                    transform: translateY(0);
                }

            }
        }

    }
}

///////////////////
// app box
.app-box {
    .wrapper-inner {
        .app-box__container {
            display: flex;
            flex-wrap: wrap;
            .app-box--item {
                // background: $ltGray url("./../src/img/bg-wire.svg");
                position: relative;
                overflow: hidden;
                display: flex;
                justify-content: center;
                flex: 1 1 calc(50% - 40px);
                align-items: center;
                margin: 10px 10px 0 0;
                padding: 10px;
                background-color: $ltGray;
                cursor: pointer;

                @include breakpoint(mobile) {
                    flex: 1 1 calc(100% - 40px);
                }

                a {
                    color: $white;

                    img {
                        transition: all 0.2s ease-in;
                    }
                }

                .app-box--item_ht {
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    background-color: rgba(12,12,12,.5);
                    transform: translate3d(-1000px, 1000px, 1000px);
                    transition: all 0.3s ease-in-out;
                }

                &:hover > .app-box--item_ht {
                    @include flexCHV;
                    transform: translate3d(0px, 0px, 0px);

                    .app__info {
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: flex-start;
                        margin-right: 50px;
                        width: 100%;
                        padding: 10px;
                        background-color: $white;
                        h4 {
                            color: $ltGray;
                            padding: 5px;
                        }
                        em {
                            display: flex;
                            span {
                                background-color: $dkGray;
                                color: yellow;
                                padding: 5px;
                            }
                        }
                    }
                }
            }
        }
    }
}

//////////////////////
// contact box
.contact-box {
    background-color: #155799;
    background-image: linear-gradient(120deg,#155799,#159957);
    .wrapper-inner {

            .contact-box__container {
                display: flex;
                flex-wrap: wrap;

                @include breakpoint(mobile) {
                    flex-direction: column;
                }

                .contact-box__container--item {
                    flex: 1 1 calc(50% - 40px);
                    margin: 5px;
                    padding: 10px;
                    background-color: $ltGray;
                }

                .contact-box__container--left {
                    @include flexCHV;
                }

                .contact-box__container--right {
                    .contact-box-form {
                        span.contact-box-form__title {
                            display: flex;
                            font-family: "ns", "Roboto", sans-serif;
                            font-size: 24px;
                            color: #144b8f;
                            line-height: 1.2;
                            padding: 20px 0px;
                        }
                        .contact-box-input {
                            width: 100%;

                            .input1 {
                                width: 100%;
                                padding: 15px;
                                margin: 10px 0;
                            }    
            
                            // textarea
                            textarea.input1 {
                                min-height: 150px;
                            }
                        }

                        .contact-box-form__container {
                            display: flex;
                            width: 100%;
                            justify-content: flex-end;
                            padding: 20px;

                            @include breakpoint(mobile) {
                                padding: 20px 0;
                            }

                            button.contact-box-form__btn {
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                width: 40%;
                                background-color: rgba(20, 75, 143, 0.7);
                                color: $white;
                                padding: 5px;
                                transition: all 0.3s ease-in-out;
                                
                                @include breakpoint(mobile) {
                                    width: 100%;
                                }
                                
                                &:hover {
                                    background-color: #144b8f;
                                }

                                span {
                                    font-family: "Roboto", sans-serif;
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;
                                    padding: 10px;
                                    span {
                                        padding: 0 5px 0 0;
                                    }

                                    svg {
                                        height: 25px;
                                        height: 25px;
                                        fill: $white;
                                    }
                                }
                            }
                        }
                        
                    }
                }
            }
    }
}

